import React, { useEffect, useRef, useState } from 'react';
import Chart from 'chart.js/auto';

const DepressiveChart = ({ data }) => {
  const chartRef = useRef(null);
  const [chartData, setChartData] = useState([]);

  useEffect(() => {
    const ctx = chartRef.current.getContext('2d');
    const chart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: chartData.map((_, index) => index + 1), // Generate labels as 1, 2, 3, ...
        datasets: [
          {
            label: 'Depressive Data',
            data: chartData,
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
            borderColor: 'rgba(75, 192, 192, 1)',
            borderWidth: 1,
          },
        ],
      },
      options: {
        responsive: true,
        scales: {
          x: {
            display: true,
            title: {
              display: true,
              text: 'Time',
            },
          },
          y: {
            display: true,
            title: {
              display: true,
              text: 'Percentage',
            },
          },
        },
      },
    });

    // Function to update the chart with new data
    const updateChart = (value) => {
      setChartData((prevChartData) => [...prevChartData, value]);
    };

    // Update the chart on each data change
    updateChart(data.depressive);

    return () => {
      chart.destroy();
    };
  }, [data]);

  return <canvas ref={chartRef} />;
};

export default DepressiveChart;
